.my-container {
    background-color: #ffffff; 
  
    position: relative;
    overflow: hidden; 
    height: 100vh;
    max-width: 100%;
    min-height: 480px;
}

.form-container {
    position: absolute;
    top: 0;
    height: 100%;
    -webkit-transition: all 0.6s ease-in-out;
    transition: all 0.6s ease-in-out;
}

.sign-in-container {
    left: 0;
    width: 50%;
    z-index: 2;
}

.sign-up-container {
    left: 0;
    width: 50%;
    opacity: 0;
    z-index: 1;
}



.my-container.right-panel-active .sign-in-container {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
}

.my-container.right-panel-active .sign-up-container {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
    opacity: 1;
    z-index: 5;
    -webkit-animation: show 0.6s;
            animation: show 0.6s;
}

@-webkit-keyframes show {
    0%,
    49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%,
    100% {
        opacity: 1;
        z-index: 5;
    }
}

@keyframes show {
    0%,
    49.99% {
        opacity: 0;
        z-index: 1;
    }
    50%,
    100% {
        opacity: 1;
        z-index: 5;
    }
}

.overlay-container {
    position: absolute;
    top: 0;
    left: 50%;
    width: 50%;
    height: 100%;
    overflow: hidden;
    -webkit-transition: -webkit-transform 0.6s ease-in-out;
    transition: -webkit-transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
    z-index: 100;
}

.my-container.right-panel-active .overlay-container {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
}

.overlay {
    background: #28a745;
    background: -webkit-linear-gradient(to right, #28a745, #28a745);
    background: -webkit-gradient(linear, left top, right top, from(#28a745), to(#28a745));
    background: -webkit-linear-gradient(left, #28a745, #28a745);
    background: linear-gradient(to right, #28a745, #28a745);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: 0 0;
    color: #ffffff;
    position: relative;
    left: -100%;
    height: 100%;
    width: 200%;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transition: -webkit-transform 0.6s ease-in-out;
    transition: -webkit-transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.my-container.right-panel-active .overlay {
    -webkit-transform: translateX(50%);
            transform: translateX(50%);
}

.overlay-panel {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 0 40px;
    text-align: center;
    top: 0;
    height: 100%;
    width: 50%;
    -webkit-transform: translateX(0);
            transform: translateX(0);
    -webkit-transition: -webkit-transform 0.6s ease-in-out;
    transition: -webkit-transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out;
    transition: transform 0.6s ease-in-out, -webkit-transform 0.6s ease-in-out;
}

.overlay-left {
    -webkit-transform: translateX(-20%);
            transform: translateX(-20%);
}

.my-container.right-panel-active .overlay-left {
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.overlay-right {
    right: 0;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}

.my-container.right-panel-active .overlay-right {
    -webkit-transform: translateX(20%);
            transform: translateX(20%);
}




@media (max-width: 768px) {
    .sign-in-container { 
